.checkout {
    padding: 20px;
    background-color: white;
    height: -webkit-max-content;
    height: max-content;
  }
  .checkout-inner{
      display: flex;
      justify-content: space-between;
      /* align-items: center; */
      padding: 0 150px;
  }
  
  .checkout-ad {
    width: 100%;
    margin-bottom: 10px;
  }
  
  .checkout-title {
    margin-right: 10px;
    padding: 10px;
    border-bottom: 1px solid lightgray;
  }
 
.checkout-product {
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  
  .checkout-product-image {
    object-fit: contain;
    width: 180px;
    height: 180px;
  }
  
  .checkout-product-info {
    padding-left: 20px;
  }
  
  .checkout-product-title {
    font-size: 20px;
    font-weight: 700;
  }
  
  .checkout-product-rating {
      display: flex;
  }
  
  .checkout-product-info > button {
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    width: 180px;
    color: #111;
    cursor: pointer;
    border-color: #a88734 #9c7e31 #846a29;
  }
  
  i {
    vertical-align: middle;
  }
.subtotal {
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    width: 300px;
    height: 150px;
    padding: 20px;
    background-color: #f3f3f3;
    border: 1px solid #dddddd;
    border-radius: 3px;
    justify-content: space-between;
  }
  
  .subtotal-gift {
    display: flex;
    align-items: center;
  }
  
  .subtotal-gift > input {
    margin-right: 5px;
  }
  
  .subtotal > button {
    background: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    color: #111;
    border-color: #a88734 #9c7e31 #846a29;
    cursor: pointer;
  }
  
